<template>
  <div ref="pieChart" class="echarts"></div>
</template>

<script>
  import echarts from 'echarts/lib/echarts'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/title'
  import 'echarts/lib/component/tooltip'
  export default {
    data() {
      return {
        pieChart: ''
      }
    },

    mounted() {
      this.myChart = echarts.init(this.$refs.pieChart)
      this.drawChart()
    },

    methods: {
      resize() {
        this.myChart.resize()
      },

      drawChart() {
        this.myChart.setOption({
          series : [
            {
              name: '访问来源',
              type: 'pie',
              radius: '80%',
              roseType: 'angle',
              data:[
                {value:235, name:'视频广告'},
                {value:274, name:'联盟广告'},
                {value:310, name:'邮件营销'},
                {value:335, name:'直接访问'},
                {value:400, name:'搜索引擎'}
              ]
            }
          ]
        })
      }
    }
  }
</script>
