<template>
  <div ref="echarts" class="echarts"></div>
</template>

<script>
  import echarts from 'echarts/lib/echarts'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/tooltip'
  export default {
    data() {
      return {
        myChart: ''
      }
    },

    mounted() {
      this.myChart = echarts.init(this.$refs.echarts)
      var week = ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
      var data1 = [11, 22, 3, 24, 15, 66, 23]
      var data2 = [9, 2, 3, 4, 5, 1, 10]
      this.drawChart(week, data1, data2)
    },

    methods: {
      resize() {
        this.myChart.resize()
      },
      
      drawChart(week, data1, data2) {
        var that = this
        var option = {
          tooltip: {
            trigger: 'axis'
          },
          grid: {
            top: '20',
            left: '10',
            right: '20',
            bottom: '5',
            containLabel: true
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: week,
            axisLabel: {
              color: '#666'
            },
            axisLine: {
              lineStyle: {
                color: '#dfdfdf'
              }
            }
          },
          color: ['#61C09F', '#409EFF'],
          yAxis: {
            type: 'value',
            axisLabel: {
              color: '#666'
            },
            axisLine: {
              lineStyle: {
                color: '#dfdfdf'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#dfdfdf'
              }
            },
            axisPointer: {
              lineStyle: {
                color: '#dfdfdf'
              }
            },
            minInterval: 1
          },
          series: [
            {
              name:'上转人数',
              type:'line',
              data: data1,
              tooltip: {
                backgroundColor: '#61C09F'
              }
            },
            {
              name:'下转人数',
              type:'line',
              data: data2,
              tooltip: {
                backgroundColor: '#61C09F'
              }
            }
          ]
      }

        this.myChart.setOption(option)
      }
    }
  }
</script>
