<template>
  <div ref="barChart" class="echarts"></div>
</template>

<script>
  import echarts from 'echarts/lib/echarts'
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/component/title'
  import 'echarts/lib/component/tooltip'
  export default {
    props: {
      color: {
        type: String,
        default: '#409EFF'
      }
    },

    data() {
      return {
        myChart: ''
      }
    },

    mounted() {
      this.myChart = echarts.init(this.$refs.barChart)
      this.drawChart()
    },

    methods: {
      resize() {
        this.myChart.resize()
      },
      drawChart() {
        var option = {
          title: {
            text: 'ECharts 入门示例'
          },
          tooltip: {},
          legend: {
            data:['销量']
          },
          xAxis: {
            data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"],
            axisLabel: {
              color: function() {
                return '#232323'
              }
            },
            axisLine: {
              lineStyle: {
                color: '#dfdfdf'
              }
            }
          },
          yAxis: {
            axisLine: {
              lineStyle: {
                color: '#dfdfdf'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#fff'
              }
            },
            axisLabel: {
              color: '#666'
            },
            minInterval: 1
          },
          series: [{
            name: '销量',
            type: 'bar',
            barMaxWidth: '42px',
            label: {
              normal: {
                show: true,
                position: 'top'
              }
            },
            data: [5, 20, 36, 10, 10, 20]
          }],
          color: ['#409EFF'],
          grid: {
            left: 50,
            bottom: 40,
            right: 20
          }
        }

        this.myChart.setOption(option)
      }
    }
  }
</script>
